body {
  padding: 0;
  margin: 0;
  font-size: 14px; }

.menu, .footer {
  background-color: #4CAF50;
  padding: 15px;
  color: #fff; }
  .menu a, .footer a {
    margin-right: 10px;
    color: #ddd; }
  .menu a:hover, .footer a:hover {
    color: #111; }

.box {
  text-align: center;
  min-height: 200px;
  font-size: 20px;
  color: blue;
  background: url("../../public/img/wdp.png") no-repeat;
  background-size: 8%;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #2196F3; }



