@-webkit-keyframes ain {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
    transform: scale(0.5); }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes ain {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
    transform: scale(0.5); }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1); } }

@-webkit-keyframes aout {
  100% {
    opacity: 0;
    -webkit-transform: scale(0.7);
    transform: scale(0.7); }
  30% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05); }
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes aout {
  100% {
    opacity: 0;
    -webkit-transform: scale(0.7);
    transform: scale(0.7); }
  30% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05); }
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

.in {
  -webkit-animation: ain .4s;
  animation: ain .4s; }

.out {
  -webkit-animation: aout .4s;
  animation: aout .4s; }

.close_box {
  cursor: pointer;
  position: absolute;
  right: 15px;
  top: 14px;
  color: white;
  height: 16px;
  width: 16px;
  line-height: initial; }

.close_box:hover .close {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg); }

.close {
  position: absolute;
  right: 7px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  background: white;
  height: 16px;
  width: 2px; }

.close:after {
  background: white;
  content: "";
  height: 2px;
  left: -7px;
  position: absolute;
  top: 7px;
  width: 16px; }

.mask {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 5000;
  background-color: black;
  opacity: 0.3; }

.popup {
  position: fixed;
  z-index: 5001;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 50px;
  border-radius: 2px; }
  .popup .title {
    text-align: left;
    padding: 0px 80px 0px 20px;
    height: 42px;
    line-height: 42px;
    border-bottom: 1px solid #eeeeee;
    font-size: 14px;
    color: white;
    overflow: hidden;
    background-color: #009688;
    border-radius: 2px 0px 0px; }



